html, body {
  font-family: "Nunito Sans", sans-serif;
  color: #1e1f28;
  background: #ffffff;
  font-weight: 400;
  font-size: 16px;
  font-feature-settings: "lnum";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #1e1f28;
  font-family: "Playfair Display", sans-serif;
  font-weight: bold;
  word-wrap: break-word;
  line-height: 1.2;
  margin: 0 0 25px 0;
}
h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span {
  display: inline-block;
}
h1 em,
h2 em,
h3 em,
h4 em,
h5 em,
h6 em {
  display: block;
  font-weight: 300;
  font-size: 1.125rem;
  font-style: italic;
}

strong {
  font-weight: 900;
}

p {
  font-size: 1rem;
  line-height: 1.7;
  font-family: "Nunito Sans", sans-serif;
  margin: 0 0 25px 0;
}

a {
  cursor: pointer;
  text-decoration: none;
}

h1 {
  font-size: 2.375rem;
}

h2 {
  font-size: 2.25rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.3125rem;
}

h6 {
  font-size: 1.125rem;
}

.color-red {
  color: #D56D5C;
}

.color-orange {
  color: #fab325;
}

.color-blue {
  color: #089DF5;
}

.color-green {
  color: #2CCB4D;
}

.link-danger {
  margin-top: 30px;
  display: block;
  text-decoration: none;
  color: #D56D5C;
}
.link-danger:hover, .link-danger:focus, .link-danger:active {
  color: #D56D5C;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
}

.flex {
  display: flex;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.align-center {
  align-items: center;
}

.justify-center {
  align-items: center;
}

.f-light {
  font-weight: 300;
}

.f-regular {
  font-weight: 400;
}

.f-medium {
  font-weight: 500;
}

.f-bold {
  font-weight: 600;
}

.f-black {
  font-weight: 900;
}

.bg-dark {
  background: #1e1f28;
}

.p-r-0 {
  padding-right: 0 !important;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #20202a;
  z-index: 999999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preloader-cover {
  position: fixed;
  background: #22222e;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.preloader-content {
  margin-bottom: 15px;
  text-align: center;
}
.preloader-content > * {
  visibility: hidden;
}
.preloader h4 {
  font-size: 1.0625rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 10px;
}
.preloader-load {
  margin-bottom: 10px;
  width: 200px;
}
.preloader .progressbar-text {
  position: relative;
  font-size: 0.875rem;
  font-weight: 400;
  color: #cacace;
  height: 300px;
  line-height: 350px;
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 0;
  margin: 0;
  transform: translate(-50%, -50%);
  color: #555;
}
.preloader .progressbar {
  stroke-dasharray: 100px, 100px;
}

