.page-head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}
.page-head nav {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

a.navbar-brand {
  padding-left: 5px;
}
@media (max-width: 767px) {
  .menu-open a.navbar-brand {
    z-index: 1;
  }
}
@media (min-width: 1200px) {
  a.navbar-brand {
    padding-left: 0;
  }
  a.navbar-brand img {
    animation: menuAnim cubic-bezier(0.42, 0, 1, 1) 0.5s forwards;
  }
}
a.navbar-brand img {
  width: 100%;
  max-width: 60px;
  height: auto;
  mix-blend-mode: difference;
  z-index: 99;
}
@media (min-width: 576px) {
  a.navbar-brand img {
    max-width: 75px;
  }
}

.page-head-wrapper {
  padding-top: 15px;
  padding-bottom: 15px;
}
.page-head-wrapper:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  top: 0;
  left: 0;
  transition: transform cubic-bezier(0.42, 0, 0.58, 1) 0.25s;
  transform: translateY(-100%);
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.2);
}
.fixed .page-head-wrapper:before {
  transform: translateY(0);
}

.navbar-nav {
  position: fixed;
  width: 100%;
  flex-direction: column;
  background: #20202a;
  left: 0;
  top: 0;
  height: 100%;
  padding-top: 150px;
  padding-left: 10px;
  transform: translateX(-100%);
  transition: transform cubic-bezier(0.42, 0, 0.58, 1) 0.25s;
}
.menu-open .navbar-nav {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .navbar-nav {
    position: relative;
    display: flex;
    padding: 0;
    width: auto;
    height: auto;
    background: transparent;
    list-style: none;
    padding-top: 15px;
    padding-left: 40px;
    height: auto;
    background: transparent;
    flex-direction: row;
    transform: none;
  }
}
.navbar-nav a {
  color: #fff;
  font-weight: bold;
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 1rem;
  display: block;
  transition: color cubic-bezier(0.42, 0, 0.58, 1) 0.25s;
}
.navbar-nav a:hover, .navbar-nav a:focus {
  color: #D56D5C;
}
.fixed .navbar-nav a {
  color: #1e1f28;
}
@media (max-width: 767px) {
