.navbar-toggler {
  position: fixed;
  top: 30px;
  right: 20px;
  background: no-repeat;
  border: 0;
  height: 30px;
  appearance: none;
  z-index: 1;
  cursor: pointer;
}
.navbar-toggler span {
  width: 36px;
  height: 2px;
  background: #fff;
  display: block;
  position: relative;
}
.fixed .navbar-toggler span {
  background: #000;
}
.menu-open .navbar-toggler span {
  background: #fff;
}
.navbar-toggler span:before, .navbar-toggler span:after {
  content: "";
  position: absolute;
  width: 36px;
  height: 2px;
  left: 0;
  background: #fff;
}
.fixed .navbar-toggler span:before, .fixed .navbar-toggler span:after {
  background: #000;
}
.menu-open .navbar-toggler span:before, .menu-open .navbar-toggler span:after {
  background: #fff;
}
.navbar-toggler span:before {
  top: -8px;
}
.navbar-toggler span:after {
  top: 8px;
}
@media (min-width: 768px) {
  .navbar-toggler {
    display: none;
  }
}

